.App {
	color: #fff;
	text-align: center;
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	font-size: calc(10px + 2vmin);
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.container {
	display: flex;
	flex-direction: column;
	row-gap: 5px;
	align-items: center;
	position: relative;
}

.img {
	border: solid 3px #ffd6d6;
}

button {
	display: flex;
	margin-top: 20px;
	/* width: 30%; */
	font-size: 24px;
	font-weight: bold;
	text-transform: uppercase;
	background-color: #ffffff00;
	border-radius: 5px;
	cursor: pointer;
	position: absolute;
	bottom: 10px;
	right: 10px;
	border: none;
	padding: 10px;
}

.hide {
	background-color: #20a6ff;
}

.btn {
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	width: 100%;
}

button:hover {
	padding: 10px;
	border-radius: 50%;
	background-color: #ffffff56;
}

.icon {
	width: 32px;
}

.posts {
	display: flex;
	align-items: center;
	justify-content: space-between;
	column-gap: 32px;
}
